
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    pro: {
      type: Object,
      default() {
        return {};
      },
    },

    isRemove: {
      type: Boolean,
      default: false,
    },
  },

  setup(props, {emit}) {
    let remove:() => void = () => {
      emit('remove');
    }
    return {
      remove
    };
  },
});
