
import axios from "axios";

import { defineComponent, ref, onMounted } from "vue";

import { useRouter } from 'vue-router'

import Product from '../components/Product.vue'

export default defineComponent({
  components: {
    Product
  },
  setup() {

    let router: any = useRouter();

    //激活的菜单类型下标
    let activeKey: any = ref<number>(0);

    //菜单类型数据
    let menuType: Array<any> = ref<any>([]);

    //商品数据
    let productData: Array<any> = ref<any>([]);

    //获取商品类型
    let getProductType: () => void = (): void => {
      axios({
        method: "get",
        url: "/type",
      })
        .then((result: any): void => {
          if (result.data.code === 400) {
            result.data.result.unshift({ type: "isHot", typeDesc: "热卖推荐" });

            (<any>menuType).value = result.data.result;

            

            let type: string = result.data.result[activeKey.value].type;
            getProductByType(type);
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //根据商品类型获取商品数据
    let getProductByType: (type: string) => void = (type: string): void => {
      let key: string = "";
      let value: string | number = "";

      if (type === "isHot") {
        key = "isHot";
        value = 1;
      } else {
        key = "type";
        value = type;
      }

      axios({
        method: "get",
        url: "/typeProducts",
        params: {
          key,
          value,
        },
      })
        .then((result: any): void => {
          if (result.data.code === 500) {
            (<any>productData).value = result.data.result;
            
          }
        })
        .catch((err: any): void => {
          
        });
    };

    //查看商品详情
    let goPage: (name: string, pid?: string) => void = ( name: string, pid?: string): void => {
      router.push({name, params: {pid}});
    }

    onMounted(() => {
      getProductType();
    });

    return {
      activeKey,
      menuType,
      productData,
      getProductByType,
      goPage
    };
  },
});
